import { createAsyncThunk } from "@reduxjs/toolkit";

import {
  getDBSnapshot,
  updateItemsInDb,
  updatePeriodsInDb,
  updateSettingsInDb,
  updateTagsInDb,
} from "db";
import { TSnapshot } from "shared/types";
import { RootState } from "shared/store";
import { saveUserData } from "shared/firebase";
import { setItems } from "features/items/slice/actions";
import { setTags } from "features/tags/slice/actions";
import { setPeriods } from "features/periods/slice/actions";
import { setSettings } from "features/settings/slice/actions";

import { syncSlice } from "./slice";

const { setConflicts: setConflictsAction } = syncSlice.actions;

export const { setSavedData, setVersion } = syncSlice.actions;

export const saveData = createAsyncThunk(
  "sync/saveData",
  async (_, thunkApi) => {
    const version = Date.now();
    await thunkApi.dispatch(setVersion(version));

    const data = await getDBSnapshot();
    await saveUserData(data);
  }
);

export const updateData = createAsyncThunk(
  "sync/updateData",
  async (data: TSnapshot | null, thunkApi) => {
    const {
      items = [],
      tags = [],
      periods = [],
      settings = {},
      version = 0,
    } = data || {};

    await thunkApi.dispatch(setVersion(version));

    await updateItemsInDb(items);
    await updateTagsInDb(tags);
    await updatePeriodsInDb(periods);
    await updateSettingsInDb(settings);

    await thunkApi.dispatch(setItems(items));
    await thunkApi.dispatch(setTags(tags));
    await thunkApi.dispatch(setPeriods(periods));
    await thunkApi.dispatch(setSettings(settings));
  }
);

export const syncWithRemote = createAsyncThunk(
  "sync/syncWithRemote",
  async (_, thunkApi) => {
    const state = (await thunkApi.getState()) as RootState;
    const {
      sync: { savedData, version },
    } = state;

    if (savedData?.version === version) return;
    await thunkApi.dispatch(setConflictsAction(true));
  }
);

export const selectLocalData = createAsyncThunk(
  "sync/selectLocalData",
  async (_, thunkApi) => {
    thunkApi.dispatch(setConflictsAction(false));
    thunkApi.dispatch(saveData());
  }
);

export const selectRemoteData = createAsyncThunk(
  "sync/selectRemoteData",
  async (_, thunkApi) => {
    const state = (await thunkApi.getState()) as RootState;
    const { savedData } = state.sync;

    thunkApi.dispatch(setConflictsAction(false));
    thunkApi.dispatch(updateData(savedData));
  }
);
