import { initializeApp } from "firebase/app";
import {
  DataSnapshot,
  DatabaseReference,
  getDatabase,
  ref,
  onValue,
  update,
  off,
} from "firebase/database";
import {
  getAuth,
  onAuthStateChanged,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  signOut,
  User,
} from "firebase/auth";

import { TSnapshot } from "shared/types";

import { firebaseConfig } from "./config";

const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const db = getDatabase(app);

let userDataRef: DatabaseReference;
let currentUser: User | null = null;
let lastSnapshot: TSnapshot | null = null;

/** Subscriptions */
const userCbs: Array<(user: User | null) => void> = [];
const dataCbs: Array<(data: TSnapshot | null) => void> = [];

/** Login or logout */
onAuthStateChanged(auth, (user) => {
  currentUser = user;
  userCbs.forEach((cb) => cb(currentUser));

  if (user) {
    userDataRef = ref(db, `userdata/${user.uid}/data`);

    /** Subscribe on user data changes in db (from other source) */
    onValue(userDataRef, (snapshot: DataSnapshot) => {
      if (snapshot.exists()) {
        lastSnapshot = snapshot.val() as TSnapshot;
      } else {
        lastSnapshot = null;
      }
      dataCbs.forEach((cb) => cb(lastSnapshot));
    });
  } else {
    if (userDataRef) {
      off(userDataRef);
    }
  }
});

/** API */

export const subscribeOnUser = (cb: (user: User | null) => void) => {
  if (currentUser) cb(currentUser);
  userCbs.push(cb);
};
export const subscribeOnData = (cb: (data: TSnapshot | null) => void) => {
  if (lastSnapshot) cb(lastSnapshot);
  dataCbs.push(cb);
};

export const signUp = (email: string, password: string) => {
  return createUserWithEmailAndPassword(auth, email, password);
};

export const login = (email: string, password: string) => {
  return signInWithEmailAndPassword(auth, email, password);
};

export const logout = () => {
  return signOut(auth);
};

export const saveUserData = async (userData: any) => {
  if (!currentUser) throw new Error("  ");
  return update(userDataRef, userData);
};
