import { useState } from "react";

import { useAppDispatch } from "shared/store";
import { Button } from "shared/ui/button";
import { Input } from "shared/ui/input";
import { login, signUp } from "features/profile/slice/actions";

import s from "./style.module.css";

type TAuthFormProps = {
  onAction: () => void
}

export const AuthForm: React.FC<TAuthFormProps> = ({ onAction }) => {
  const dispatch = useAppDispatch();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSignUp = () => {
    if (!email || !password) return;
    dispatch(signUp({ email, password }));
    onAction()
  };

  const handleLogin = () => {
    if (!email || !password) return;
    dispatch(login({ email, password }));
    onAction()
  };

  return (
    <form>
      <div className={s.Inputs}>
        <Input value={email} onChange={setEmail} placeholder="Email" />
        <Input value={password} onChange={setPassword} placeholder="Password" />
      </div>

      <div className={s.Actions}>
        <Button onClick={handleLogin}></Button>
        <Button onClick={handleSignUp}></Button>
      </div>
    </form>
  );
};
