import { createAsyncThunk } from "@reduxjs/toolkit";

import { saveData } from "features/sync/slice/actions";
import { addNotification } from "features/notifier/slice/actions";
import { TSettings } from "entities/settings/types";
import { NotificationStatuses } from "entities/notification/types";

import { settingsSlice } from "./slice";

const { updateSettings: updateSettingsAction } = settingsSlice.actions;
export const { setSettings } = settingsSlice.actions;

export const updateSettings = createAsyncThunk(
  "settings/updateSettings",
  async (settings: Partial<TSettings>, thunkApi) => {
    await thunkApi.dispatch(updateSettingsAction(settings));
    await thunkApi.dispatch(saveData());
    await thunkApi.dispatch(
      addNotification({
        text: " ",
        status: NotificationStatuses.success,
      })
    );
  }
);
