import { createAsyncThunk } from "@reduxjs/toolkit";

import { TNewPeriodData, TPeriod } from "entities/period/types";
import { NotificationStatuses } from "entities/notification/types";
import { saveData } from "features/sync/slice/actions";
import { addNotification } from "features/notifier/slice/actions";

import { periodsSlice } from "./slice";

const {
  addPeriod: addPeriodAction,
  updatePeriod: updatePeriodAction,
  removePeriod: removePeriodAction,
} = periodsSlice.actions;

export const { setPeriods, setActivePeriod } = periodsSlice.actions;

export const addPeriod = createAsyncThunk(
  "periods/addPeriod",
  async (period: TNewPeriodData, thunkApi) => {
    await thunkApi.dispatch(addPeriodAction(period));
    await thunkApi.dispatch(saveData());
    await thunkApi.dispatch(
      addNotification({
        text: "  ",
        status: NotificationStatuses.success,
      })
    );
  }
);

export const updatePeriod = createAsyncThunk(
  "periods/updatePeriod",
  async (data: { period: TPeriod; data: TNewPeriodData }, thunkApi) => {
    await thunkApi.dispatch(updatePeriodAction(data));
    await thunkApi.dispatch(saveData());
  }
);

export const removePeriod = createAsyncThunk(
  "periods/removePeriod",
  async (period: TPeriod, thunkApi) => {
    await thunkApi.dispatch(removePeriodAction(period));
    await thunkApi.dispatch(saveData());
  }
);
