import { useEffect, useRef } from "react";
import { useSelector } from "react-redux";

import { useAppDispatch } from "shared/store";
import { TModalRef } from "shared/types";
import { Button } from "shared/ui/button";
import { Modal } from "shared/ui/modal/index";
import { selectUser } from "features/profile/slice/selectors";
import { logout } from "features/profile/slice/actions";
import { AuthForm } from "features/profile/ui/authForm";
import { selectIsAuthOpen } from "features/view/slice/selectors";
import { closeAuth, openAuth } from "features/view/slice/actions";

export const AuthStatus = () => {
  const dispatch = useAppDispatch();
  const user = useSelector(selectUser);
  const isAuthOpen = useSelector(selectIsAuthOpen);

  const modalRef = useRef<TModalRef | null>(null);

  const onOpenModal = () => {
    dispatch(openAuth());
  };

  const onCloseModal = () => {
    dispatch(closeAuth());
  };

  const handleLogout = () => {
    dispatch(logout());
  };

  const renderStatus = () => {
    if (!user) {
      return (
        <div>
          <div>  </div>
          <Button onClick={onOpenModal}></Button>
        </div>
      );
    }

    return (
      <div>
        <div>
          <span>
               <b>{user.email}</b>
          </span>
          &nbsp;
          <Button size="s" invert onClick={handleLogout}>
              
          </Button>
        </div>
      </div>
    );
  };

  useEffect(() => {
    if (isAuthOpen) modalRef.current?.open();
    else modalRef.current?.close();
  }, [isAuthOpen]);

  return (
    <>
      {renderStatus()}
      <Modal ref={modalRef} title="" onClose={onCloseModal}>
        <AuthForm onAction={onCloseModal} />
      </Modal>
    </>
  );
};
