import { createAsyncThunk } from "@reduxjs/toolkit";

import { TNewTagData, TTag } from "entities/tag/types";
import { NotificationStatuses } from "entities/notification/types";
import { saveData } from "features/sync/slice/actions";
import { addNotification } from "features/notifier/slice/actions";

import { tagsSlice } from "./slice";

const {
  addTag: addTagAction,
  updateTag: updateTagAction,
  removeTag: removeTagAction,
} = tagsSlice.actions;
export const { setTags } = tagsSlice.actions;

export const addTag = createAsyncThunk(
  "tags/addTag",
  async (tag: TNewTagData, thunkApi) => {
    await thunkApi.dispatch(addTagAction(tag));
    await thunkApi.dispatch(saveData());
    await thunkApi.dispatch(
      addNotification({
        text: " ",
        status: NotificationStatuses.success,
      })
    );
  }
);

export const updateTag = createAsyncThunk(
  "tags/updateTag",
  async (data: { tag: TTag; data: TNewTagData }, thunkApi) => {
    await thunkApi.dispatch(updateTagAction(data));
    await thunkApi.dispatch(saveData());
  }
);

export const removeTag = createAsyncThunk(
  "tags/removeTag",
  async (tag: TTag, thunkApi) => {
    await thunkApi.dispatch(removeTagAction(tag));
    await thunkApi.dispatch(saveData());
    await thunkApi.dispatch(
      addNotification({
        text: " ",
        status: NotificationStatuses.success,
      })
    );
  }
);
