import { createSlice, PayloadAction } from "@reduxjs/toolkit";

import { updateVersionInDb } from "db";
import { TSnapshot } from "shared/types";

type TSyncState = {
  savedData: TSnapshot | null;
  hasConflicts: boolean;
  version: number;
};

const initialState: TSyncState = {
  savedData: null,
  hasConflicts: false,
  version: 0,
};

export const syncSlice = createSlice({
  name: "sync",
  initialState,
  reducers: {
    setSavedData(state, action: PayloadAction<TSnapshot | null>) {
      state.savedData = action.payload;
    },
    setConflicts(state, action: PayloadAction<boolean>) {
      state.hasConflicts = action.payload;
    },
    setVersion(state, action: PayloadAction<number>) {
      state.version = action.payload;
      updateVersionInDb(action.payload);
    },
  },
});
