import { createAsyncThunk } from "@reduxjs/toolkit";

import { TItem, TNewItemData } from "entities/item/types";
import { NotificationStatuses } from "entities/notification/types";
import { saveData } from "features/sync/slice/actions";
import { addNotification } from "features/notifier/slice/actions";

import { itemsSlice } from "./slice";

const {
  addItem: addItemAction,
  addItems: addItemsAction,
  updateItem: updateItemAction,
  removeItem: removeItemAction,
} = itemsSlice.actions;

export const { setItems } = itemsSlice.actions;

export const addItem = createAsyncThunk(
  "items/addItem",
  async (item: TNewItemData, thunkApi) => {
    await thunkApi.dispatch(addItemAction(item));
    await thunkApi.dispatch(saveData());
    await thunkApi.dispatch(
      addNotification({
        text: " ",
        status: NotificationStatuses.success,
      })
    );
  }
);

export const addItems = createAsyncThunk(
  "items/addItems",
  async (items: TNewItemData[], thunkApi) => {
    await thunkApi.dispatch(addItemsAction(items));
    await thunkApi.dispatch(saveData());
  }
);

export const updateItem = createAsyncThunk(
  "items/updateItem",
  async (data: { item: TItem; data: TNewItemData }, thunkApi) => {
    await thunkApi.dispatch(updateItemAction(data));
    await thunkApi.dispatch(saveData());
  }
);

export const removeItem = createAsyncThunk(
  "items/removeItem",
  async (item: TItem, thunkApi) => {
    await thunkApi.dispatch(removeItemAction(item));
    await thunkApi.dispatch(saveData());
    await thunkApi.dispatch(
      addNotification({
        text: " ",
        status: NotificationStatuses.success,
      })
    );
  }
);
