import React from "react";
import { Provider } from "react-redux";
import { RouterProvider } from "react-router-dom";
import ReactDOM from "react-dom/client";

import { addPeriodToDb, initDB, updatePeriodInDb } from "db";
import { subscribeOnUser, subscribeOnData } from "shared/firebase";
import { setItems } from "features/items/slice/actions";
import { setTags } from "features/tags/slice/actions";
import { setPeriods } from "features/periods/slice/actions";
import { setSettings } from "features/settings/slice/actions";
import { initUser, setUser } from "features/profile/slice/actions";
import {
  setSavedData,
  syncWithRemote,
  setVersion,
} from "features/sync/slice/actions";
import { createRouter } from "app/router";

import store from "./store";

export default function init() {
  const router = createRouter();

  initDB().then(({ tags, items, periods, settings, version }) => {
    if (!periods.length || periods[0].createdAt === null) {
      const firstItemDate = items.reduce((min, item) => {
        let minDate = Math.min(min, item.createdAt);

        (item.subitems || []).forEach((subitem) => {
          minDate = Math.min(minDate, subitem.createdAt);
        });

        return minDate;
      }, +new Date());

      if (!periods.length) {
        const period = {
          id: 0,
          createdAt: firstItemDate,
          name: "",
        };

        periods.push(period);

        addPeriodToDb(period);
      } else {
        periods[0].createdAt = firstItemDate;
        updatePeriodInDb(periods[0]);
      }
    }

    store.dispatch(setItems(items));
    store.dispatch(setTags(tags));
    store.dispatch(setPeriods(periods));
    store.dispatch(setSettings(settings));
    store.dispatch(setVersion(version));

    subscribeOnUser((user) => {
      if (user) {
        const { uid, email } = user;
        store.dispatch(setUser({ uid, email }));
      } else {
        store.dispatch(setUser(null));
      }
    });

    subscribeOnData((data) => {
      const {
        profile: { user, inited },
      } = store.getState();
      store.dispatch(setSavedData(data || null));
      if (user && !inited) {
        store.dispatch(initUser());
      }
      store.dispatch(syncWithRemote());
    });
  });

  const root = ReactDOM.createRoot(
    document.getElementById("root") as HTMLElement
  );

  root.render(
    <React.StrictMode>
      <Provider store={store}>
        <RouterProvider router={router} />
      </Provider>
    </React.StrictMode>
  );
}
